.flex {
  display: flex;
}
.flexDirectionRow {
  flex-direction: row;
  -webkit-flex-direction: row;
}
.flexDirectionColumn {
  flex-direction: column;
  -webkit-flex-direction: column;
}
.contentCenter {
  justify-content: center;
}
.contentStart {
  justify-content: flex-start;
}
.contentEnd {
  justify-content: end;
}
.contentSpaceBetween {
  justify-content: space-between;
}
.justifySelfCenter {
  justify-self: center;
}
.justifySelfStart {
  justify-self: flex-start;
}
.justifySelfEnd {
  justify-self: flex-end;
}
.itemsCenter {
  align-items: center;
}
.itemsStart {
  align-items: flex-start;
}
.itemsEnd {
  align-items: flex-end;
}

.centerCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.fullWidth {
  width: 100%;
}
.fullHeight {
  height: 100%;
}
.fullfull {
  width: 100%;
  height: 100%;
}
